.stars {
  display: flex;

  &__star {
    background-image: url("../../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
    width: 16px;
    height: 16px;
    border-radius: 0.5px;
  }

  &--1 :nth-child(1),
  &--2 :nth-child(-n+2),
  &--3 :nth-child(-n+3),
  &--4 :nth-child(-n+4),
  &--5 :nth-child(-n+5) {
    background-image: url("../../images/star-active.svg");
  }
}
